nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  background: #0b6e4f;
  color: #fff;
}

nav h1 {
  margin: 0;
  font-size: 1.25rem;
}

nav ul {
  list-style: none;
  display: flex;
  gap: 1rem;
  margin: 0;
  padding: 0;
}

nav input[type="text"] {
  padding: 0.35rem 0.5rem;
  border-radius: 4px;
  border: none;
}

@media (max-width: 600px) {
  nav ul {
    display: none;
  }
}
