/* ===== App.css ===== */
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background-color: #f8fdf8;
  color: #222;
}

/* Navbar */
nav {
  background-color: #1c572b;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 40px;
}

nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

nav li {
  cursor: pointer;
}

nav input {
  padding: 6px 10px;
  border-radius: 4px;
  border: none;
}

/* Hero Section */
.hero {
  text-align: center;
  background-color: #e6f4e6;
  padding: 80px 20px;
}

.hero h2 {
  font-size: 2.5rem;
  color: #145214;
}

.hero p {
  max-width: 600px;
  margin: 10px auto;
  color: #444;
}

.stats {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-top: 40px;
}

.stat-card {
  background: white;
  padding: 20px 30px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Features */
.features {
  background-color: #1c572b;
  color: white;
  text-align: center;
  padding: 80px 20px;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  max-width: 800px;
  margin: 40px auto;
}

.feature-card {
  background-color: #2b7a38;
  padding: 20px;
  border-radius: 10px;
}

/* Testimonials */
.testimonials {
  background-color: #f0fdf0;
  text-align: center;
  padding: 60px 20px;
}

.testimonial-card {
  background: white;
  max-width: 500px;
  margin: auto;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Footer */
footer {
  background-color: #1c572b;
  color: white;
  text-align: center;
  padding: 20px;
  margin-top: 60px;
}
